import axios from 'axios';
import LogUtils from './../logUtils/index';
require('promise.prototype.finally').shim();
/**
 * 
 * axios  TODO NEW AXIOS
 */
export default class HttpRequestUtils {
    /**
     * @description axios axios
     * @param fn
     * axios HttpRequestUtils class
     * @return { class } HttpRequestUtils 
     */
    static init(fn) {
        LogUtils.logInfo('https://alie-z.github.io/mUtils/#/', `[mUtils] http_request HttpRequestUtils.init aixos => `);
        if (fn && typeof fn === 'function') {
            fn.call(null, axios, HttpRequestUtils);
        }
        HttpRequestUtils.isInit = true;
        return HttpRequestUtils;
    }
    /**
     * @description get
     * @param { string } url url
     * @param { object } params 
     * @param { object } config axios
     * @return { Promise }
     */
    static async get(url, params, config) {
        return new Promise((resolve, reject) => {
            if (!HttpRequestUtils.isInit) {
                LogUtils.logError('HttpRequestUtils.isInit()', `[mUtils] http_request: => error`);
                reject('HttpRequestUtils.isInit()');
            }
            const getInfo = Object.assign({}, { params }, config);
            axios.get(url, getInfo).then((res) => {
                LogUtils.group(`[mUtils] http_request get  => ${url}`, LogUtils.successColor);
                LogUtils.logInfo(res.data, `http_request response => `);
                LogUtils.logInfo(res.config, `http_request config => `);
                LogUtils.logInfo(res.config.params, `http_request params => `);
                LogUtils.groupEnd();
                resolve(res);
            }).catch(async (e) => {
                LogUtils.group(`[mUtils] http_request get  ${url}`, LogUtils.errorColor);
                LogUtils.logInfo(e, `http_request error => `);
                LogUtils.groupEnd();
                resolve(e);
            });
        });
    }
    /**
     * @description post
     * @param { string } url url
     * @param { object } data 
     * @param { object } config axios
     * @return { Promise }
     */
    static post(url, data, config) {
        return new Promise((resolve, reject) => {
            if (!HttpRequestUtils.isInit) {
                LogUtils.logError('HttpRequestUtils.isInit()', `[mUtils] http_request error => `);
                reject('HttpRequestUtils.isInit()');
            }
            const postInfo = Object.assign({}, { data: data }, config);
            axios.post(url, postInfo).then((res) => {
                LogUtils.group(`[mUtils] http_request post  => ${url}`, LogUtils.successColor);
                LogUtils.logInfo(res.data, `http_request response => `);
                LogUtils.logInfo(res.config, `http_request config => `);
                LogUtils.logInfo(res.config.params, `http_request params => `);
                LogUtils.groupEnd();
                resolve(res);
            }).catch((e) => {
                LogUtils.group(`[mUtils] http_request post  => ${url}`, LogUtils.errorColor);
                LogUtils.logInfo(e, `http_request error => `);
                LogUtils.groupEnd();
                reject(e);
            });
        });
    }
}
/**
 * CODE
 */
HttpRequestUtils.successCode = 200;
/**
 * url
 */
HttpRequestUtils.baseUrl = '';
/**
 * timeout
 */
HttpRequestUtils.timeOut = 15000;
/**
 * 
 */
HttpRequestUtils.isInit = false;
